.grid {
  width: 100%;
  margin: 0 auto;
}
.header {
  //height: 100vh;
  background: url("img/bg-header.jpg") center top / cover;
  padding-top: 35px;
  padding-bottom: 500px;
  color: $color-white;
      &__content {
        margin-top: 300px;
        text-align: center;
          &-title {
          font-size: 72px;
            font-weight: normal;
            text-transform: uppercase;
          }
          &-text {
            width: 700px;
            font-size: 30px;
            font-style: italic;
            text-align: center;
            margin: 0 auto;
          }
      }
}
.navbar {
  display: flex !important;
  justify-content: space-between !important;
  color: $color-white;
    & .nav-link {
      transition: all 1s;
    }
    & .nav-item:hover .nav-link{
      color: black !important;
      transform: rotate(720deg);
    }

}
.collapse.navbar-collapse{
  justify-content: flex-end !important;
  color: $color-white;
    & a {
      font-size: 24px;
      color: $color-white !important;
    }
}

@media (min-width: 992px)
{
  .grid {
    width: 1110px;
  }
}

@media (max-width: 1110px)
{
  .grid {
    width: 100%;
  }
}
@media (max-width: 700px)
{
  .header__content-text {
    width: 100%;
  }
}
@media (max-width: 375px) {
  .header {
    padding-bottom: 100px;
  }
  .header__content {
    margin-top: 30px;
  }
}