.footer {
  padding: 75px 0;
  background-color: $color-contact;
  position: relative;
      & .grid {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
      }
      & span {
        font-size: 18px;
        color: $color-white;
      }
      & ul {
        display: flex;
        & li {
          list-style: none;
          &:not(:last-child){
            margin-right: 25px;
          }
        }
      }
}

.js-click {
  display: flex;
  align-items: center;
  background-color: $color-contact;
  opacity: 0.502;
  padding: 25px;
  position: fixed;
  right: 5%;
  bottom: 30%;
  cursor: pointer;
  opacity: 0;
  transition: all 1s;
}
@media (max-width: 1024px) {
  .footer .grid {
    padding: 0 15px;
  }
}
@media (max-width: 768px)
{
  .footer .grid {
    padding: 0 15px;
  }

}
@media (max-width: 375px)
{
    .footer {
      padding: 25px 0;
    }
    .footer .grid {
      justify-content: center;
        & span {
          margin-bottom: 10px;
        }
    }
    .contact__form textarea {
      margin-bottom: 10px;
    }
  .contact__btn {
    padding: 10px;
    font-size: 15px;
  }
  .js-click {
    padding: 10px;
  }
  .contact__map-item-3 {
    top: 38%;
  }

}