.contact {
  text-align: center;
  padding-top: 50px;
  background-color: $color-white-bold;
        &__form {
          margin: 0 auto;
          margin-top: 275px;
          width: 870px;
                & input {
                  background-color: #dad8e3;
                  opacity: 0.502;
                  border: none;
                  border-radius: 4px;
                  color: $color-white;
                  font-size: 18px;
                  font-style: italic;
                  width: calc(50% - 15px);
                  padding: 20px 25px;
                  margin-bottom: 25px;
                      &:focus {
                        opacity: 1;
                      }


                  &:first-child {
                      margin-right: 30px;
                    }
                  &::placeholder{color: $color-white;};
                }
                 & textarea {
                   background-color: #dad8e3;
                   opacity: 0.502;
                   font-size: 18px;
                   font-style: italic;
                   color: $color-white;
                   padding: 20px 25px;
                   border: none;
                   border-radius: 4px;
                   width: 100%;
                   margin-bottom: 50px;
                   &:focus {
                     opacity: 1;
                   }
                   &::placeholder{color: $color-white;};
                 }
        }
        &__map {
          background: url("img/bg-map.jpg") center center no-repeat / cover;
          padding: 50px 0;
          text-align: center;
          position: relative;
              &-item {
                    display: flex;
                    position: absolute;
                    left: 100%;
                    transform: translateX(-100%);
                  &-1 {
                    background-color: $color-contact;
                    padding: 25px;
                    display: flex;
                    align-items: center;
                  }
                  &-2 {
                    background-color: #dad8e3;
                    opacity: 0.502;
                    padding: 15px 40px;
                    width: 260px;
                    & p {
                      color: $color-p;
                      margin: 0;
                    }
                  }
                  &-3 {
                    background-color: $color-contact;
                    padding: 25px;
                    position: absolute;
                    left: 100%;
                    top: 35%;
                    transform: translateX(-100%);
                  }
              }
            & h3 {
              color: $color-white !important;
              font-weight: normal;
              margin-bottom: 50px;
            }
        }

          &__slider {
              width: 100%;

                &__grid {
                  position: relative;
                  width: 100%;
                  margin-bottom: 50px;
                //  align-items: center;
                //  & button {
                //    width: 22px;
                //    border: none;
                //    background-color: transparent;
                //  }
                }
          }
      & h3 {
        color: #665f60;
        font-weight: normal;
        margin-top: 35px;
        margin-bottom: 70px;
      }
    &__people {
      //display: flex;
      //justify-content: space-between;
      //transition: all 1s;
          &-item {
            border-top-left-radius: 50%;
            border-top-right-radius: 50%;
            position: relative;
            text-align: center;
            width: 328px;
            background-color: $color-white;
            margin-left: 15px;
            margin-right: 15px;

                  &:not(:last-child) {
                    //margin-right: 30px;
                  }
              &>img {
                margin-top: -5px;
                display: inline-block !important;
              }
            & ul {
              margin-bottom: 0 !important;
              background-color: $color-contact;
            }
            & h4 {
              font-size: 24px;
              color: $color-contact;
              margin-top: 35px;
              margin-bottom: 35px;
            }
            & p {
              font-size: 24px;
              color: #665f60;
              margin-bottom: 55px;
            }
          }
    }
        &__btn {
          background-color: $color-contact;
          padding: 25px 70px;
          border-radius: 4px;
          border: none;
          font-size: 20px;
          color: $color-white;
          transition: all 1s;
            &:hover {
              border: 2px solid red;
              color: red;
              border-top-left-radius: 50%;
              border-bottom-right-radius: 50%;
            }
        }
}
.slider__btn__prev {
  position: absolute;
  top: 50%;
  left: -20px;

}
.slider__btn__next {
  position: absolute;
  top: 50%;
  right: -20px;
}

@media (max-width: 1160px)
{
  .slider__btn__prev {
    top: 10%;
    left: 15px;
  }
  .slider__btn__next {
    top: 10%;
    right: 15px;
  }
}
@media (max-width: 870px)
{
  .contact__form {
    width: 100%;
  }
}
@media (max-width: 375px)
{
  .contact__people-item {
    margin-left: 0;
    margin-right: 0;
  }

}