* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: $font-lato;
}
.bold {
  font-weight: bold;
}
.js-btn {
  padding: 20px 55px;
  font-size: 20px;
  font-style: italic;
  color: #7d7a7c;
    &:focus {
      outline: none;
    }
}
.grey {
  background-color: #dbdbdb;
}
h3,h4 {
  font-weight: bold;
}
h3 {
  font-size: 36px;
}
h4 {
  font-size: 24px;
}
p {
  font-size: 18px;
}
.js-popubchik {
  margin-bottom: 50px;
  display: none;
}
.active {
  display: block;
}
.active-item {
  background-color: white;
}
.navbar-light .navbar-toggler {
  border-color: white !important;
}

.social__list {
  display: flex;
  justify-content: center;
  padding: 15px 0;
      & li {
        list-style: none;
          &:not(:last-child){
            margin-right: 10px;
          }
      }
}
button {
  border: none;
  background-color: transparent;
}

