.works {

  padding: 50px 0;
  text-align: center;
  background: url("img/bg-works.jpg") center top / cover;

      & img {
        margin-bottom: 30px;
      }
      & h3 {
        color: $color-white;
        margin-bottom: 65px;
      }
  &__slider {
    width: 730px;
    height: 550px;
    position: relative;
    margin: 0 auto;
    margin-bottom: 45px;
        & img {
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          opacity: 0;
          transition: opacity 1.5s;
        }
        & img.shower {
          opacity: 1;
        }
        &-item {
          display: flex;
          justify-content: center;
          margin-bottom: 50px;
            & li {
              display: block;
              width: 10px;
              height: 10px;
              border: 1px solid white;
              border-radius: 50%;
              &:not(:last-child) {
                margin-right: 5px;
              }
            }
        }


  }
}
.grid-slider {
  display: flex;
  align-items: center;
  & button {
    width: 22px;
    height: 38px;
    background-color: transparent;
    border: none;
    &:focus {
      outline: none !important;
    }
    &:hover {
      transform: scale(1.5);
    }
  }
}
.work-btn {
  border: 2px solid white !important;
  padding: 10px 45px;
  color: white;
  background-color: transparent;
  transition: all 1s;
    &:hover {
      transform: rotateX(360deg);
      background-color: black;
      color: white;
    }
}

@media (max-width: 800px)
{
  .works button:hover {
    transform: none;
  }

}