.about {
  background-color: $color-white-bold;
  padding-top: 120px;
        &__description {
          display: flex;
          padding-bottom: 120px;
        }
  &__img {
    position: relative;
      &>img {
        width: 100%;
      }
  }

  &__title {
    color: $color-white;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 70px;
    left: 110px;
        & img {
          margin-bottom: 40px;
        }
  }
  &__text {
    width: 640px;
    margin-left: 125px;
    margin-top: 55px;
        & p {
          font-size: 20px;
          color: #665f60;
        }
        & .js-btn {
          display: block;
          border: 2px solid #665f60;
          margin: 90px auto 0px auto;
          transition: all 1s;
          &:hover {
            border-radius: 50%;
            background-color: #665f60;
            color: white;
          }
        }
  }
  &__gallery {
    display: flex;
          &-block1 {
            display: flex;
            flex-wrap: wrap;
            width: 66.6%;
                &-row1 {
                  display: flex;
                  width: 100%;
                }
                &-row2 {
                  display: flex;
                  width: 100%;
                }
                &-item {
                  color: #665f60;
                  text-align: center;
                  padding-top: 90px;
                  width: 369px;
                  height: 400px;
                    & h4 {
                      margin-top: 20px;
                    }
                    & p {
                      font-size: 17px;
                      font-style: italic;
                    }
                }
          }
          &-block2 {
            margin-left: -2px;
            text-align: center;
            padding-top: 90px;
            width: 33.3%;
            height: 800px;
            background: url("img/about-2.jpg") center center no-repeat / cover;


            color: $color-white;
              & p {
                margin-top: 120px;
                font-size: 17px;
                font-style: italic;
              }
          }
  }
}

@media (max-width: 1108px)
{
  .about__gallery-block1 {
    width: 50%;
      &-item {
        width: 100%;
      }
    &-row1 {
      display: flex;

      flex-wrap: wrap;
    }
    &-row2 {
      margin-left: -2px;
      position: relative;
      display: block;
        & .about__gallery-block1-item:first-child {

          position: absolute;
          left: 100%;
          top: 0;
        }
        & .about__gallery-block1-item {
          position: relative;
          left: 0;
        }
    }

  }
  .about__gallery-block2 {
    width: 50%;
  }

}

@media (max-width: 850px)
{
  .about__description {
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
  }
  .about__text {
    width: 100%;
    padding: 0 10px;
    margin-left: 0;
  }
}
@media (max-width: 375px)
{
  .about {
    padding-top: 0;
  }
}